import { useEffect, useRef, useState } from "react";
import styled from "styled-components";
import arrow from "../../assets/images/Arrow.svg";

const SSlider = styled.div`
  display: flex;
  flex-direction: column;
  gap: 25px;
  width: 100%;
  justify-content: space-between;
  align-items: center;

  & > div:last-child {
    display: flex;
    gap: 60px;
  }

  & > div > img {
    opacity: 0.9;
    transition: 0.25s;
    user-select: none;
  }

  & > div > img:hover {
    opacity: 1;
  }
`;

const SliderBox = styled.div`
  position: relative;
  width: 100%;
  max-width: 550px;
  min-height: 315px;
  overflow: hidden;
  display: flex;
  align-items: center;
  
  scroll-snap-type: x mandatory;
  overflow-x: scroll;

  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
  &::-webkit-scrollbar {
    display: none;
  }
`;

const SliderInner = styled.div`
  transition: 0.75s ease-in-out;
  & > div > div {
    scroll-snap-align: start;
  }
`;

const ArrowLeft = styled.img`
  cursor: pointer;
  transform: rotate(180deg);
  width: 15px;
`;

const ArrowRight = styled.img`
  cursor: pointer;
  width: 15px;
`;

const Slider = ({ stepMargin, steps, children }) => {
  // const [step, setStep] = useState(0);
  // useEffect(() => {
  //   if (step > steps) setStep(0);
  //   if (step < 0) setStep(steps);

  //   Inner.current.setAttribute(
  //     "style",
  //     `transform: translateX(-${ step == 0 ? 0 : stepMargin * step}px)`
  //   );
  // }, [step]);

  const Inner = useRef(null);

  return (
    <SSlider>
      <SliderBox>
        <SliderInner ref={Inner} style={{ marginLeft: 0 }}>
          {children}
        </SliderInner>
      </SliderBox>
      {/* <div>
        <ArrowLeft
          src={arrow}
          alt="arrowLeft"
          onClick={() => setStep(step - 1)}
        />
        <ArrowRight
          src={arrow}
          alt="arrowRight"
          onClick={() => setStep(step + 1)}
        />
      </div> */}
    </SSlider>
  );
};

export default Slider;
