import styled from "styled-components";

import { EngLoc } from "../../../assets/constants/localizations";

import { Title2 } from "../../../assets/styles/component";
import Slider from "../SliderMobile";
import Toggler from "../TogglerMobile";

import Emirates_logo from "../../../assets/images/Emirates_logo.svg";
import Huawei_logo from "../../../assets/images/Huawei_logo.svg";
import Hyundai_logo from "../../../assets/images/Hyundai_logo.svg";
import Mega_logo from "../../../assets/images/mega_logo.svg";
import Moscow24_logo from "../../../assets/images/Moscow24_logo.svg";
import Perekryostok_logo from "../../../assets/images/Perekryostok_logo.svg";
import Premier_logo from "../../../assets/images/premier_logo.svg";
import SberZvuk_logo from "../../../assets/images/SberZvuk_logo.svg";
import Sts_logo from "../../../assets/images/sts_logo.svg";
import Vkusvill_logo from "../../../assets/images/Vkusvill_logo.svg";
import X5Group_logo from "../../../assets/images/X5Group_logo.svg";
import OZON_logo from "../../../assets/images/OZON_logo.svg";
import { useRef } from "react";

const SPartnersInner = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 60px;
  margin: 0 15px;
`;

const PartnerLogo = styled.div`
  width: 100%;
  min-width: 260px;
  height: 180px;
  min-height: 150px;

  background: rgba(255, 255, 255, 0.15);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 28px;

  display: flex;
  justify-content: center;
  align-items: center;

  user-select: none;

  & img {
    width: 50%;
  }

  @media (max-width: 600px) {
    width: calc(45vw - 5px);
    min-width: 200px;
  }

  @media (max-width: 450px) {
    height: 220px;
    width: calc(100vw - 40px);

    & img {
      width: 30%;
    }
  }
`;

const LogosBox = styled.div`
  display: flex;
  gap: 20px;
  margin-bottom: 20px;
  width: fit-content;
`;

const Title = styled(Title2)`
  font-size: 48px;
  font-weight: 400;
  text-align: center;
`;

const PartnersInner = ({language}) => {
  const Logos1 = [
    Huawei_logo,
    Hyundai_logo,
    X5Group_logo,
    Premier_logo,
    Perekryostok_logo,
    OZON_logo,
    Sts_logo,
    Emirates_logo,
    Perekryostok_logo,
    OZON_logo,
    Sts_logo,
    Emirates_logo,
  ];

  const Logos2 = [
    Emirates_logo,
    Huawei_logo,
    Hyundai_logo,
    X5Group_logo,
    Premier_logo,
    Mega_logo,
    Moscow24_logo,
    SberZvuk_logo,
    Vkusvill_logo,
    Perekryostok_logo,
    OZON_logo,
    Sts_logo,
  ];

  const stepMargins = () => {
    let margin = 1620;
    let screenWidth = window.screen.width;

    if (screenWidth > 600 && screenWidth < 940) margin = 282;
    if (screenWidth < 600)
      margin = logoRef.current && logoRef.current.offsetWidth + 20;

    return margin;
  };

  const stepsCount = () => {
    let screenWidth = window.screen.width;
    let steps = 2;

    if (screenWidth > 450 && screenWidth < 940) steps = 5;
    if (screenWidth < 450) steps = 11;

    return steps;
  };

  const logoRef = useRef(null);

  return (
    <SPartnersInner>
      <Title>{language === "Ru" ? "С нами работают" : EngLoc.partners.partners_slider.title}</Title>
      <Toggler language={language}></Toggler>
      <Slider stepMargin={stepMargins()} steps={stepsCount()}>
        <LogosBox>
          {Logos1.map((logo) => (
            <PartnerLogo ref={logoRef} key={Math.random()}>
              <img src={logo} loading="lazy" alt="partner"/>
            </PartnerLogo>
          ))}
        </LogosBox>
        <LogosBox>
          {Logos2.map((logo) => (
            <PartnerLogo key={Math.random()}>
              <img src={logo} loading="lazy" alt="partner" />
            </PartnerLogo>
          ))}
        </LogosBox>
      </Slider>
    </SPartnersInner>
  );
};

export default PartnersInner;
