import React from "react";
import styled from "styled-components";
import { EngLoc } from "../../../assets/constants/localizations";
import { Text, Title3 } from "../../../assets/styles/component";
import Button from "../../../assets/UI/ButtonMobile";

const SCase = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 15px;

  position: relative;

  width: calc(100vw - 95px);
  max-width: 480px;
  min-height: 357px;
  padding: 30px;

  text-align: left;
  border: 2px solid #ffffff2a;
  border-radius: 34px;

  & > *:not(:last-child) {
    z-index: 1;
  }
`;

const Title = styled(Title3)`
  line-height: 110%;
  font-size: 35px;
  font-weight: 400;
  text-align: center;
`;

const Description = styled(Text)``;

const Categories = styled.div`
  display: flex;
  flex-direction: column;
  gap: 15px;
  margin-bottom: 15px;
`;

const Category = styled.div``;

const TitleValue = styled(Text)``;

const Value = styled(Text)``;

const Case = React.forwardRef(
  ({
    title,
    desc,
    design,
    value1,
    value2,
    value3,
    valueTitle1,
    valueTitle2,
    valueTitle3,
    language,
  }, ref) => {
    return (
      <SCase ref={ref}>
        <Title>{title}</Title>
        <Description>{desc}</Description>
        <Categories>
          {value1 && (
            <Category>
              <TitleValue color="#FFFFFF80">
                {valueTitle1
                  ? valueTitle1
                  : language === "Ru"
                  ? "Кампания"
                  : EngLoc.partners.cases.period}
              </TitleValue>
              <Value size={36} weight={600}>
                {value1}
              </Value>
            </Category>
          )}
          {value2 && (
            <Category>
              <TitleValue color="#FFFFFF80">
                {valueTitle2
                  ? valueTitle2
                  : language === "Ru"
                  ? "Подписчиков"
                  : EngLoc.partners.cases.subscribers}
              </TitleValue>
              <Value size={36} weight={600}>
                {value2}
              </Value>
            </Category>
          )}
          {value3 && (
            <Category>
              <TitleValue color="#FFFFFF80">
                {valueTitle3
                  ? valueTitle3
                  : language === "Ru"
                  ? "Просмотров"
                  : EngLoc.partners.cases.views
                }
              </TitleValue>
              <Value size={36} weight={600}>
                {value3}
              </Value>
            </Category>
          )}
        </Categories>
        <Button type="secondary" link={true}>
          {language === "Ru" ? "Подробнее" : EngLoc.buttons.more}
        </Button>
        {design}
      </SCase>
    );
  }
);

export default Case;
